import React, { Component } from 'react'
import {deleteToken} from './../../../Utility'
import { Link } from 'react-router-dom'


export class Sidebar extends Component {

    render() {
        return (
            <React.Fragment>
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <h2 className="navbar-brand" href="#a">Bienvenido <span className="text-danger">{this.props.user.username}</span></h2>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    {
                        this.props.user.role_id === 1 ?
                        <>
                            <li className="nav-item dropdown ml-auto pr-5">
                                <a className="nav-link dropdown-toggle text-primary" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Opciones
                                </a>
                                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <Link className="dropdown-item" to="/home">Ir a Carpetas</Link>
                                    <Link className="dropdown-item" to="/users">Ir a Usuarios</Link>
                                <div className="dropdown-divider"></div>
                                    <a onClick={deleteToken} className="dropdown-item" href="#">Cerrar Sesion</a>
                                </div>
                            </li>
                        </>
                        :
                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav ml-auto">
                                    <li className="nav-item active">
                                        <a onClick={deleteToken} className="nav-link" href="#a">Cerrar Sesion</a>
                                    </li>
                                </ul>
                            </div>
                    }
                    
                    
                    
                </nav>
            </React.Fragment>
        )
    }
}

export default Sidebar
